<script>
import Nav from "@/components/Nav.vue";
import Facedat from "@jx3box/jx3box-facedat/src/Facedat";
import Upload from "@jx3box/jx3box-facedat/src/Upload";
export default {
    name: "Facedata",
    props: [],
    data: function () {
        return {
            data: "",
            done: false,
        };
    },
    computed: {
        json: function () {
            return this.data && this.data.json;
        },
    },
    methods: {
        handleSuccess: function (data) {
            this.data = data;
            if(data?.json){
                this.done = true
            }else{
                this.done = false
            }
        },
    },
    mounted: function () {},
    components: {
        Facedat,
        Upload,
        Nav,
    },
};
</script>

